import { Component, OnInit } from '@angular/core';
import { AssetAssetService, GaTerimaItemService, VendorVendorService, AssetStatusService, AssetPartIdService, AssetModelOwnerService, AssetModelOwnerMasterService, AssetModelxService, AssetBinLocationService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Modelx, Status, Part_id, Asset, Terima_item, Model_owner_master, Model_owner, Bin_location } from 'src/app/sdk/asset/models';
import { Vendor } from 'src/app/sdk/user/models';
import { FormControl, Validators } from '@angular/forms';
import { DirektoriPegawaiMasterService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { Pegawai_master, Kantor } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private assetAssetService: AssetAssetService,
        private gaTerimaItemService: GaTerimaItemService,
        private assetModelxService: AssetModelxService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private assetModelOwnerService: AssetModelOwnerService,
        private companyKantorService: CompanyKantorService,
        private vendorVendorService: VendorVendorService,
        private assetStatusService: AssetStatusService,
        private assetBinLocationService: AssetBinLocationService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activeRouter: ActivatedRoute,
    ) { }

    dataTerima: Terima_item = {}
    dataAsset: Asset

    listVendor: Vendor[] = []
    listStatus: Status[] = []
    listOwner: Model_owner[] = []
    listKantor: Kantor[] = []
    listBin: Bin_location[] = []


    id_terima_item: string = ''
    ngOnInit() {
        this.activeRouter.paramMap.subscribe(
            data => {
                if (data.get('id_terima_item')) {
                    this.id_terima_item = data.get('id_terima_item')
                }
            }
        )

        this.getItem()
        this.getPreeData()
        this.form()
    }

    id_asset: string = ''
    getItem() {
        this.gaTerimaItemService.getGaTerimaItemsId(this.id_terima_item).subscribe(
            data=> {
                this.dataTerima = data.data;
                this.id_asset = this.dataTerima.link_id_asset;
                this.dataForm.patchValue({id_asset: this.dataTerima.link_id_asset})
                this.getAsset()
            }
        )
    }


    getPreeData() {
        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
        })
        this.assetStatusService.getAssetStatuses().subscribe(data => {
            this.listStatus = data.data
        })
        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })
        this.assetBinLocationService.getAssetBinLocations().subscribe(data => {
            this.listBin = data.data
        })


    }

    id_model: string = ''
    getAsset() {
        this.assetAssetService.getAssetAssetsId(this.id_asset).subscribe(
            data=> {
                this.dataForm.setValue(data.data);
                this.id_model = data.data.id_model;
                this.getModel();
                this.getOwner();
            }
        )
    }


    model: Modelx = {}

    getModel() {
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data=> {
                this.model =data.data
            }
        )
    }
    getOwner() {
        this.assetModelOwnerService.getAssetModelOwners(JSON.stringify({id_model: this.id_model})).subscribe(data => {
            this.listOwner = data.data
        })
    }

    dataForm: FormGroupTypeSafe<Asset>
    form() {

        this.dataForm = this.fb.group<Asset>({
            id_model: new FormControl(''),
            sn: new FormControl(''),
            id_status: new FormControl('', [Validators.required]),
            id_vendor: new FormControl(''),
            ref_input: new FormControl('app'),
            tag: new FormControl(''),
            for_module_position: new FormControl('kantor'),
            for_id_module_position: new FormControl('0'),
            purchase_number: new FormControl(''),
            purchase_date: new FormControl(''),
            manufacture_date: new FormControl(''),
            price: new FormControl(''),
            id_asset: new FormControl(''),
            id_bin_location: new FormControl(''),
            id_currency: new FormControl(''),
            id_kantor: new FormControl(''),
            id_owner: new FormControl(''),
            id_user: new FormControl(''),
            for_module_date: new FormControl(''),
            for_module_ref_id: new FormControl(''),
            created_at: new FormControl(''),
            purchase_condition: new FormControl(''),
            updated_at: new FormControl(''),
            qty: new FormControl(''),
            ref_id_input: new FormControl(''),
        })
    }

    getValue(key){
        return this.dataForm.get(key).value
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }



        this.dataAsset = this.dataForm.value
        this.assetAssetService.putAssetAssetsId({id: this.id_asset, body: this.dataAsset}).subscribe(
            data => {
                this.UbahStatus()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    status: string = 'close';
    UbahStatus() {


        this.spinner.show()
        let params: Terima_item = {
            id_terima_item: this.dataTerima.id_terima_item,
            status: this.status,
            link_id_asset: this.dataTerima.link_id_asset,
            from_id_module: this.dataTerima.from_id_module,
            from_id_module_detail: this.dataTerima.from_id_module_detail,
            from_modul: this.dataTerima.from_modul,
        }

        this.gaTerimaItemService.putGaTerimaItemsId({id:this.id_terima_item, body: params}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('Data tidak valid')
            }
        )

    }


    goToBack() {
        this.router.navigate(['/asset/management/received/list'])
    }



}
